import { isValidElement } from "react";

export function isChildrenElement(children: React.ReactNode) {
  if (Array.isArray(children)) return false;
  switch (typeof children) {
    default:
      return false;
    case "string":
    case "number":
    case "bigint":
    case "boolean":
    case "undefined":
      return false;
    case "object":
    case "function":
    case "symbol":
      return isValidElement(children);
  }
}
