import {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
  cloneElement,
} from "react";
import {
  useMeasure,
  useClickAway,
  useMouseWheel,
  useWindowScroll,
  useScroll,
} from "react-use";
import styled from "styled-components";
import { Portal } from "../Siblings";
import { TDropdownPosition, useDropdownPosition } from "./useDropdownPosition";
import useCurrent from "../hooks/useCurrent";
import { dropdownPortalID } from "../Siblings/portalID";
import { isChildrenElement } from "../utils/isChildrenElement";

export interface DropdownProps {
  /**
   * trigger component
   */
  children?: React.ReactNode;
  /**
   * @default "click"
   */
  triggerType?: "click" | "hover";
  /**
   * dropdown component
   */
  overlay?: React.ReactNode;
  /**
   * trigger wrapper styles
   */
  style?: React.CSSProperties;
  /**
   * dropdown wrapper styles
   */
  overlayContainerStyle?: React.CSSProperties;
  /**
   * @default undefined = auto
   * dropdown position: "top-left" | "top-right" | "bottom-left" | "bottom-right" | "top" | "bottom" | undefined = auto
   */
  position?: TDropdownPosition;
  /**
   * dropdown visible
   */
  visible?: boolean;
  /**
   * dropdown visible onChange callback
   */
  onVisibleChange?: (visible: boolean) => void;
  /**
   * disable the auto dismiss when user click or scroll outside
   */
  disableAutoDismiss?: boolean;
  /**
   * disable dropdown enter & leave default animation
   */
  disableAnimation?: boolean;
  /**
   * ID of the dom element, which will be insert the dropdown items
   * @default dropdownPortalID (`orthogonal-dropdown-overlay`)
   */
  parentNodeID?: string;
  /** the scroll element ref, add listener to watch scroll event */
  scrollRef?: React.RefObject<HTMLElement>;
  /**
   * @default false
   * @description when set to true, overlay position should cover trigger
   */
  fitToTrigger?: boolean;
}

export function BaseDropdown(props: DropdownProps) {
  const {
    overlayContainerStyle,
    visible,
    onVisibleChange,
    disableAnimation,
    disableAutoDismiss,
    parentNodeID = dropdownPortalID,
    scrollRef,
    triggerType = "click",
    fitToTrigger = false,
  } = props;
  const isHover = useRef(false);
  const [overlayRef, { height: overlayHeight, width: overlayWidth }] =
    useMeasure();
  const onVisibleCallbackRef = useCurrent(onVisibleChange);
  const [_visible, setVisible] = useState(!!visible);
  // sync with parent state
  useEffect(() => {
    setVisible((curVisible) => {
      if (curVisible !== visible) return !!visible;
      return curVisible;
    });
  }, [visible]);
  useEffect(() => {
    if (onVisibleCallbackRef.current) onVisibleCallbackRef.current(_visible);
  }, [_visible, onVisibleCallbackRef]);
  const initd = !!overlayHeight && !!overlayWidth;
  const { ref, top, left, onHoverUpdateRect, isTop } = useDropdownPosition(
    overlayHeight,
    overlayWidth,
    32, // default height, will be replace automatically when get the overlay rect height
    false,
    props.position,
    fitToTrigger
  );
  /** @tip handle auto dismiss when click */
  useClickAway(ref, () => {
    if (disableAutoDismiss) return;
    if (isHover.current) return;
    setVisible(false);
  });
  /** @tip handle auto dismiss when mouse wheel */
  const mouseWheel = useMouseWheel();
  useEffect(() => {
    if (disableAutoDismiss) {
      onHoverUpdateRect();
      return;
    }
    if (isHover.current) return;
    if (!mouseWheel) return;
    setVisible(false);
  }, [mouseWheel, disableAutoDismiss, onHoverUpdateRect]);
  /** @tip handle auto dismiss when scroll */
  const { x, y } = useWindowScroll();
  const _scrollRef = useRef(null);
  const { x: containerX, y: containerY } = useScroll(scrollRef || _scrollRef);
  useEffect(() => {
    if (disableAutoDismiss) {
      onHoverUpdateRect();
      return;
    }
    if (isHover.current) return;
    if (!x && !y && !containerX && !containerY) return;
    setVisible(false);
  }, [disableAutoDismiss, onHoverUpdateRect, x, y, containerX, containerY]);
  const _overlayContainerStyle: React.CSSProperties = useMemo(() => {
    const opacity = initd && _visible ? 1 : 0;
    const pointerEvents = initd && _visible ? "auto" : "none";
    const transitionTopOffset = fitToTrigger ? 1 : 1.1;
    const transitionBottomOffset = fitToTrigger ? 1 : 0.9;
    let _top = _visible ? top : top * transitionBottomOffset;
    if (isTop) _top = _visible ? top : top * transitionTopOffset;
    if (disableAnimation) _top = top;
    return {
      transition: disableAnimation ? "none" : undefined,
      pointerEvents,
      opacity,
      ...(overlayContainerStyle || {}),
      zIndex: _visible ? overlayContainerStyle?.zIndex : -1,
      top: _top,
      left,
    };
  }, [
    initd,
    _visible,
    top,
    isTop,
    disableAnimation,
    overlayContainerStyle,
    left,
    fitToTrigger,
  ]);
  const _onMouseEnter = useCallback(() => {
    isHover.current = true;
    if (triggerType === "hover") setVisible(true);
    onHoverUpdateRect();
  }, [onHoverUpdateRect, triggerType]);
  const isSingleElement = isChildrenElement(props.children);
  return (
    <>
      {/* will generate extra wrapper */}
      {isSingleElement &&
        cloneElement(props.children as unknown as JSX.Element, {
          ref: (element: Element) => {
            // function
            if (
              "ref" in (props.children as any) &&
              typeof (props.children as any).ref === "function"
            ) {
              (props.children as any).ref(element);
            }
            // useRef
            if (
              "ref" in (props.children as any) &&
              typeof (props.children as any).ref === "object" &&
              (props.children as any).ref
            ) {
              if ("current" in (props.children as any).ref)
                (props.children as any).ref.current = element;
            }
            (ref.current as any) = element;
          },
          onMouseEnter: (e: MouseEvent) => {
            if ((props.children as any).props.onMouseEnter)
              (props.children as any).props.onMouseEnter(e);
            _onMouseEnter();
          },
          onMouseLeave: (e: MouseEvent) => {
            if ((props.children as any).props.onMouseLeave)
              (props.children as any).props.onMouseLeave(e);
            isHover.current = false;
            if (triggerType === "hover") setVisible(false);
          },
        })}
      {/* will not generate extra wrapper */}
      {!isSingleElement && (
        <Wrapper
          ref={ref}
          style={props.style}
          onMouseEnter={_onMouseEnter}
          onMouseLeave={() => {
            isHover.current = false;
            if (triggerType === "hover") setVisible(false);
          }}
          role="select"
        >
          {props.children}
        </Wrapper>
      )}
      {!!props.overlay && (
        <Portal id={parentNodeID}>
          <OverlayWrapper
            style={_overlayContainerStyle}
            ref={(el) => el && overlayRef(el)}
            onMouseEnter={_onMouseEnter}
            onMouseLeave={() => {
              isHover.current = false;
              if (triggerType === "hover") setVisible(false);
            }}
          >
            {props.overlay}
          </OverlayWrapper>
        </Portal>
      )}
    </>
  );
}

const Wrapper = styled.div`
  position: relative;
  display: flex;
  flex: none;
  width: max-content;
  align-items: center;
  box-sizing: border-box;
`;

const OverlayWrapper = styled.div`
  position: fixed;
  z-index: ${(p) => p.theme.zIndexes.dropdownZIndex};
  width: max-content;
  box-sizing: border-box;
  max-height: 80vh;
  transition: top 0.2s ease, opacity 0.2s ease;
`;
